<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/Br'}">患者管理</el-breadcrumb-item>
      <el-breadcrumb-item>病历</el-breadcrumb-item>
    </el-breadcrumb>
    <el-page-header @back="$router.back()" content="病历" style="margin-top: 20px"/>
    <div>
      <el-descriptions class="margin-top" :column="3" border style="width: 70%;margin-top: 20px">
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-user"></i>
            姓名
          </template>
          {{ this.brInfo.name }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-tickets"></i>
            性别
          </template>
          <el-tag size="small">{{ this.brInfo.sex }}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-tickets"></i>
            年龄
          </template>
          {{ this.brInfo.age }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-mobile-phone"></i>
            手机号
          </template>
          {{ this.brInfo.tel }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-location-outline"></i>
            地址
          </template>
          {{ this.brInfo.address }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-office-building"></i>
            来源
          </template>
          {{ this.brInfo.bfrom }}
        </el-descriptions-item>
      </el-descriptions>
    </div>
    <div style="margin-bottom: 20px;margin-top: 20px">
      <el-date-picker
          v-model="btime"
          value-format="yyyy-MM-dd HH:mm:ss"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
          :clearable="false"
          @change="getBE()">
      </el-date-picker>
      <el-select v-model="params.btype" placeholder="请选择就诊类型" style="margin-left: 10px">
        <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
      </el-select>
      <el-button type="success" style="margin-left: 10px" @click="findBl()">查询所有</el-button>
      <el-button type="primary" style="margin-left: 10px" @click="searchWf()">查询未付</el-button>
      <el-button type="danger" style="margin-left: 10px" @click="clearSearch()">清空</el-button>
      <el-button type="primary" style="margin-left: 80px" @click="addBl()">添加</el-button>
    </div>
    <div>
      <el-table
          :data="tableData"
          stripe
          border
          size="mini"
          style="width: 70%;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" class="demo-table-expand">
              <el-form-item label="时间">
                <span>{{ props.row.btime }}</span>
              </el-form-item>
              <el-form-item label="就诊类型">
                <span>{{ props.row.btype }}</span>
              </el-form-item>
              <el-form-item label="病情">
                <span>{{ props.row.bdetail }}</span>
              </el-form-item>
              <el-form-item label="费用">
                <span>{{ props.row.fy }}</span>
              </el-form-item>
              <el-form-item label="未付">
                <template slot-scope="scope">
                  <el-tag type="danger" v-if="props.row.wf>0">{{ props.row.wf }}</el-tag>
                  <el-tag type="success" v-else>{{ props.row.wf }}</el-tag>
                </template>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="btime"
            label="时间">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="btype"
            label="就诊类型">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="bdetail"
            label="病情">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="fy"
            label="费用">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="wf"
            label="未付">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.wf>0">{{ scope.row.wf }}</el-tag>
            <el-tag type="success" v-else>{{ scope.row.wf }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            fixed="right"
            width=200
            label="操作">
          <template slot-scope="scope">
            <el-button type="primary" @click="editBl(scope.row)">编辑</el-button>
            <el-popconfirm title="确定删除吗？" @confirm="delBl(scope.row.id)">
              <el-button slot="reference" type="danger" style="margin-left: 10px">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[5, 10, 50, 100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total"
          style="margin-top: 20px"
      >
      </el-pagination>
    </div>
    <div>
      <el-dialog title="请填写信息" :visible.sync="dialogFormVisible" width="30%" :before-close="CancelDialog"
                 @open="more++">
        <el-form :key="more" :model="form" :rules="rules" ref="form" label-width="100px">
          <el-form-item label="时间" label-width="15%" prop="btime">
            <el-date-picker
                v-model="form.btime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="类型" label-width="15%" prop="btype">
            <el-select v-model="form.btype" placeholder="请选择">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="病情" label-width="15%" prop="bdetail">
            <el-input
                type="textarea"
                :autosize="{ minRows: 2}"
                placeholder="请输入内容"
                v-model="form.bdetail">
            </el-input>
          </el-form-item>
          <el-form-item label="费用" label-width="15%" prop="fy">
            <el-input v-model="form.fy" autocomplete="off" style="width: 70%" type="number"></el-input>
          </el-form-item>
          <el-form-item label="未付" label-width="15%" prop="wf">
            <el-input v-model="form.wf" autocomplete="off" style="width: 70%" type="number"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelDialog">取 消</el-button>
          <el-button type="success" @click="resetRef">重置</el-button>
          <el-button type="primary" @click="submitBl()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "BlView",
  props: ['bid'],
  data() {
    return {
      brInfo: {},
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      btime: '',
      params: {
        brid: this.bid,
        sbtime: '',
        ebtime: '',
        btype: '',
        wf: '',
        pageNum: 1,
        pageSize: 5
      },
      total: 0,
      tableData: [],
      dialogFormVisible: false,
      form: {},
      rules: {
        btime: [
          {required: true, message: '请选择时间', trigger: blur}
        ],
        btype: [
          {required: true, message: '请选择就诊类型', trigger: blur}
        ],
        bdetail: [],
        fy: [
          {required: true, message: '请输入费用', trigger: blur}
        ],
        wf: [
          {required: true, message: '请输入未付费用', trigger: blur}
        ],
      },
      more: 0,
      options: [{
        value: '洗牙',
        label: '洗牙'
      }, {
        value: '拔牙',
        label: '拔牙'
      }, {
        value: '补牙',
        label: '补牙'
      }, {
        value: '根管治疗',
        label: '根管治疗'
      }, {
        value: '固定义齿修复',
        label: '固定义齿修复'
      }, {
        value: '活动义齿修复',
        label: '活动义齿修复'
      }, {
        value: '种植牙',
        label: '种植牙'
      }, {
        value: '牙齿矫正',
        label: '牙齿矫正'
      }]
    }
  },
  created() {
    request.get("/findBr/" + this.bid).then(res => {
      if (res.state === 0) {
        this.brInfo = res.data;
      } else {
        this.$message.error(res.message);
      }
    })
    this.findBl();
  },
  methods: {
    load() {
      request.get("/findAllBl").then(res => {
        if (res.state === 0) {
          this.tableData = res.data;
        }
      })
    },

    getBE() {
      if (this.btime !== '') {
        this.params.sbtime = this.btime[0].toString();
        this.params.ebtime = this.btime[1].toString();
      }
    },

    findBl() {
      request.get("/findBl", {
        params: this.params
      }).then(res => {
        if (res.state === 0) {
          this.tableData = res.data.list;
          this.total = res.data.total;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
    },

    searchWf() {
      this.params.wf = 'wf';
      this.findBl();
    },

    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBl()
    },

    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBl()
    },

    clearSearch() {
      this.btime = ''
      this.params = {
        brid: this.bid,
        pageNum: 1,
        pageSize: 5,
        sbtime: '',
        ebtime: '',
        btype: '',
        wf: ''
      }
      this.findBl()
    },

    addBl() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    editBl(obj) {
      this.form = obj;
      this.dialogFormVisible = true;
    },
    submitBl() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          request.post("/saveBl/" + this.bid, this.form).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              this.dialogFormVisible = false
              this.findBl()
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    },

    delBl(id) {
      request.delete("/delBl/" + id).then(res => {
        if (res.state === 0) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.findBl()
        } else {
          this.$message.error(res.message);
        }
      })
    },

    resetRef() {
      this.$refs.form.resetFields();
    },

    CancelDialog() {
      this.dialogFormVisible = false;
      this.resetRef();
    },

    ts() {
      request.get("/ts", {
        params: this.params
      }).then(res => {
        console.log(res.data)
      })
    }
  },

  watch: {
    btime: {
      handler(val) {
        if (!val) {
          this.btime = ''
          this.params.sbtime = ''
          this.params.ebtime = ''
        }
      }
    }
  }
}
</script>

<style scoped>

</style>